@charset "UTF-8";
*,
*::before,
*::after {
  box-sizing: border-box;
}
html {
  scroll-behavior: smooth;
}
body {
  font-family: "Lato", sans-serif;
  font-size: 16px;
  color: #01050B;
  margin: 0;
  padding: 0;
}
h1,
.h1,
h2,
.h2,
h3,
.h3,
.sub-24,
.sub-20 {
  font-family: "Barlow", sans-serif;
  font-weight: 700;
}
.btn,
.button,
.wp-block-button__link {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 14px 18px;
  border-radius: 8px;
  cursor: pointer;
  text-decoration: none;
  font-family: "Barlow", sans-serif;
  font-weight: 700;
  font-size: 16px;
  text-transform: uppercase;
  white-space: nowrap;
  transition: all 0.2s ease;
}
.btn span,
.button span,
.wp-block-button__link span {
  position: relative;
  padding-right: 15px;
}
.btn span:after,
.button span:after,
.wp-block-button__link span:after {
  content: '';
  position: absolute;
  -webkit-mask-image: url('../../img/arrow.svg');
  -webkit-mask-size: contain;
  -webkit-mask-repeat: no-repeat;
  mask-image: url('../../img/arrow.svg');
  mask-size: contain;
  mask-repeat: no-repeat;
  transition: background-color 0.2s ease;
  top: 61%;
  transform: translateY(-50%);
  right: 0;
  height: 8px;
  width: 10px;
}
.btn--primary,
.button--primary {
  background-color: #B22029;
  color: #ffffff;
  border: 1px solid #B22029;
}
.btn--primary span:after,
.button--primary span:after {
  background-color: #ffffff;
}
.btn--primary:hover,
.button--primary:hover {
  background-color: transparent;
  color: #B22029;
}
.btn--primary:hover span:after,
.button--primary:hover span:after {
  background-color: #B22029;
}
.btn--secondary,
.button--secondary {
  background-color: #ffffff;
  color: #000000;
  border: 1px solid #ffffff;
}
.btn--secondary span:after,
.button--secondary span:after {
  background-color: #000000;
}
.btn--secondary:hover,
.button--secondary:hover {
  background-color: transparent;
  color: #ffffff;
}
.btn--secondary:hover span:after,
.button--secondary:hover span:after {
  background-color: #ffffff;
}
.container,
.container-strong {
  width: 100%!important;
  max-width: 1280px!important;
  padding-left: 24px!important;
  padding-right: 24px!important;
  margin: 0 auto!important;
}
