/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}
.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}
.slick-list:focus {
  outline: none;
}
.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}
.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}
.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
  margin-left: auto;
  margin-right: auto;
}
.slick-track:before,
.slick-track:after {
  content: "";
  display: table;
}
.slick-track:after {
  clear: both;
}
.slick-loading .slick-track {
  visibility: hidden;
}
.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none;
}
[dir="rtl"] .slick-slide {
  float: right;
}
.slick-slide img {
  display: block;
}
.slick-slide.slick-loading img {
  display: none;
}
.slick-slide.dragging img {
  pointer-events: none;
}
.slick-initialized .slick-slide {
  display: block;
}
.slick-loading .slick-slide {
  visibility: hidden;
}
.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}
.slick-arrow.slick-hidden {
  display: none;
}
/* Slider */
.slick-loading .slick-list {
  background: #fff url('./ajax-loader.gif') center center no-repeat;
}
/* Arrows */
.slick-prev,
.slick-next {
  position: absolute;
  display: block;
  height: 20px;
  width: 20px;
  line-height: 0px;
  font-size: 0px;
  cursor: pointer;
  background: transparent;
  color: transparent;
  top: 50%;
  -webkit-transform: translate(0, -50%);
  -ms-transform: translate(0, -50%);
  transform: translate(0, -50%);
  padding: 0;
  border: none;
  outline: none;
}
.slick-prev:hover,
.slick-next:hover,
.slick-prev:focus,
.slick-next:focus {
  outline: none;
  background: transparent;
  color: transparent;
}
.slick-prev:hover:before,
.slick-next:hover:before,
.slick-prev:focus:before,
.slick-next:focus:before {
  opacity: 1;
}
.slick-prev.slick-disabled:before,
.slick-next.slick-disabled:before {
  opacity: 0.25;
}
.slick-prev:before,
.slick-next:before {
  font-family: "slick";
  font-size: 20px;
  line-height: 1;
  color: white;
  opacity: 0.75;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* Icons */
}
@font-face {
  font-family: 'slick';
  font-weight: normal;
  font-style: normal;
  src: url('./fonts/slick.eot');
  src: url('./fonts/slick.eot?#iefix') format('embedded-opentype'), url('./fonts/slick.woff') format('woff'), url('./fonts/slick.ttf') format('truetype'), url('./fonts/slick.svg#slick') format('svg');
}
.slick-prev {
  left: -25px;
}
[dir="rtl"] .slick-prev {
  left: auto;
  right: -25px;
}
.slick-prev:before {
  content: "←";
}
[dir="rtl"] .slick-prev:before {
  content: "→";
}
.slick-next {
  right: -25px;
}
[dir="rtl"] .slick-next {
  left: -25px;
  right: auto;
}
.slick-next:before {
  content: "→";
}
[dir="rtl"] .slick-next:before {
  content: "←";
}
/* Dots */
.slick-dotted .slick-slider {
  margin-bottom: 30px;
}
.slick-dots {
  position: absolute;
  bottom: -25px;
  list-style: none;
  display: block;
  text-align: center;
  padding: 0;
  margin: 0;
  width: 100%;
}
.slick-dots li {
  position: relative;
  display: inline-block;
  height: 20px;
  width: 20px;
  margin: 0 5px;
  padding: 0;
  cursor: pointer;
}
.slick-dots li button {
  border: 0;
  background: transparent;
  display: block;
  height: 20px;
  width: 20px;
  outline: none;
  line-height: 0px;
  font-size: 0px;
  color: transparent;
  padding: 5px;
  cursor: pointer;
}
.slick-dots li button:hover,
.slick-dots li button:focus {
  outline: none;
}
.slick-dots li button:hover:before,
.slick-dots li button:focus:before {
  opacity: 1;
}
.slick-dots li button:before {
  position: absolute;
  top: 0;
  left: 0;
  content: "•";
  width: 20px;
  height: 20px;
  font-family: "slick";
  font-size: 6px;
  line-height: 20px;
  text-align: center;
  color: black;
  opacity: 0.25;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.slick-dots li.slick-active button:before {
  color: black;
  opacity: 0.75;
}
.site-header {
  background-color: #020406;
  color: #ffffff;
}
.site-header__inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 32px;
  position: relative;
  padding-top: 20px;
  padding-bottom: 20px;
}
.site-header__logo-link {
  display: inline-flex;
  align-items: center;
  text-decoration: none;
  color: #ffffff;
}
.site-header__logo-text {
  text-transform: uppercase;
  font-size: 14px;
}
.site-header__panel {
  display: flex;
  align-items: center;
}
.site-header__nav .primary-menu {
  display: flex;
  justify-content: center;
  gap: 32px;
  list-style: none;
  margin: 0;
  padding: 0;
}
.site-header__nav .primary-menu a {
  font-family: "Barlow", sans-serif;
  text-transform: uppercase;
  color: #ffffff;
  text-decoration: none;
  font-size: clamp(14px, 1.3vw, 16px);
  font-weight: 700;
  white-space: nowrap;
  transition: all 0.2s ease;
}
.site-header__nav .primary-menu a:hover {
  color: #B22029;
}
.site-header__actions {
  display: flex;
  align-items: center;
  gap: 12px;
}
.site-header__cta {
  border-radius: 4px;
}
.site-header__login {
  border: 1px solid #ffffff;
  color: #ffffff;
  padding: 13px 30px;
  transition: all 0.2s ease;
  border-radius: 4px;
}
.site-header__login:hover {
  border: 1px solid #B22029;
  color: #B22029;
}
.site-header__login:hover span:before {
  background-color: #B22029;
}
.site-header__login span {
  position: relative;
  padding-left: 20px;
  padding-right: 0;
}
.site-header__login span:before {
  content: '';
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 0;
  height: 16px;
  width: 13px;
  background-color: #ffffff;
  -webkit-mask-image: url('../../img/user.svg');
  -webkit-mask-size: contain;
  -webkit-mask-repeat: no-repeat;
  mask-image: url('../../img/user.svg');
  mask-size: contain;
  mask-repeat: no-repeat;
  transition: background-color 0.2s ease;
}
.site-header__login span:after {
  content: none;
}
@media (max-width: 1023px) {
  .site-header.is-open .cw-header__burger span:nth-child(1) {
    transform: translateY(13px) rotate(45deg);
  }
  .site-header.is-open .cw-header__burger span:nth-child(2) {
    opacity: 0;
  }
  .site-header.is-open .cw-header__burger span:nth-child(3) {
    transform: translateY(-6px) rotate(-45deg);
  }
  .site-header.is-open .cw-header__panel {
    display: flex;
  }
  .site-header__inner {
    padding-top: 35px;
    padding-bottom: 35px;
  }
  .site-header__nav .primary-menu {
    flex-direction: column;
  }
  .site-header__nav .primary-menu a {
    font-size: 16px;
  }
  .site-header__actions {
    width: 100%;
    flex-direction: column;
    gap: 20px;
  }
  .site-header__logo {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
  }
  .cw-header__panel {
    display: none;
    position: absolute;
    top: 100%;
    left: 0;
    right: 0;
    background: #020406;
    padding: 16px 24px 24px;
    box-shadow: 0 16px 40px rgba(15, 23, 42, 0.08);
    flex-direction: column;
    align-items: flex-start;
    gap: 30px;
    z-index: 2;
  }
  .cw-header__burger {
    display: inline-flex;
    flex-direction: column;
    justify-content: center;
    width: 32px;
    height: 32px;
    border: 0;
    background: 0 0;
    padding: 0;
  }
  .cw-header__burger span {
    height: 2px;
    margin: 4px 0;
    background: #B22029;
    border-radius: 999px;
    transition: transform 0.2s, opacity 0.2s;
  }
}
@media (min-width: 1024px) {
  .site-header__nav {
    flex: 1 1 auto;
  }
  .site-header__nav .primary-menu {
    align-items: center;
  }
  .cw-header__burger {
    display: none;
  }
  .cw-header__panel {
    display: flex;
    align-items: center;
    gap: 30px;
    flex-wrap: wrap;
    justify-content: end;
  }
}
