.hero {
  position: relative;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  color: #ffffff;
  overflow: hidden;
}
.hero__overlay {
  position: absolute;
  inset: 0;
  pointer-events: none;
}
.hero__inner {
  position: relative;
  z-index: 1;
}
.hero__eyebrow {
  font-size: clamp(16px, 1.8vw, 20px);
  font-family: "Barlow Bold", sans-serif;
  font-weight: 700;
  text-transform: uppercase;
  margin: 0 0 20px;
}
.hero__title {
  font-size: clamp(32px, 4.8vw, 64px);
  text-transform: uppercase;
  margin: 0 0 15px;
}
.hero__subtitle {
  font-size: clamp(16px, 1.8vw, 20px);
  margin: 0 0 40px;
}
.hero__actions {
  display: flex;
  gap: 16px;
  flex-wrap: wrap;
}
.hero__btn {
  min-width: 150px;
}
.hero-features__inner {
  display: flex;
  justify-content: center;
}
.hero-features__item {
  display: inline-flex;
  align-items: center;
  gap: 12px;
}
.hero-features__label {
  font-family: "Barlow Bold", sans-serif;
  font-weight: 500;
  font-size: 16px;
  white-space: nowrap;
  text-transform: uppercase;
}
.hero-features__icon img {
  object-fit: contain;
  display: block;
  width: 30px;
  height: 30px;
}
@media (max-width: 767px) {
  .hero {
    padding: 60px 0;
  }
  .hero__overlay {
    background: linear-gradient(90deg, rgba(0, 0, 0, 0.8) 0%, rgba(0, 0, 0, 0.4) 60%, rgba(0, 0, 0, 0.2) 100%);
  }
  .hero__content {
    max-width: 100%;
  }
}
@media (max-width: 1023px) {
  .hero-features {
    position: relative;
    margin-top: 30px;
  }
  .hero-features__inner {
    flex-direction: column;
    gap: 30px;
  }
}
@media (min-width: 768px) {
  .hero {
    padding: 160px 0;
  }
  .hero__overlay {
    height: 300px;
    background: linear-gradient(180deg, #000 0%, rgba(0, 0, 0, 0) 100%);
  }
  .hero__content {
    max-width: 560px;
  }
}
@media (min-width: 1024px) {
  .hero-features {
    position: absolute;
    bottom: 0;
    padding: 50px 0;
    left: 50%;
    transform: translateX(-50%);
  }
  .hero-features__item:not(:last-child) {
    border-right: 1px solid rgba(255, 255, 255, 0.2);
    padding-right: 55px;
    margin-right: 55px;
  }
