.steps {
  text-align: center;
}
.steps__eyebrow {
  font-family: "Barlow", sans-serif;
  font-weight: 700;
  font-size: 16px;
  letter-spacing: 3.2px;
  text-transform: uppercase;
  color: #B22029;
  margin: 0 0 16px;
}
.steps__title {
  font-family: "Barlow", sans-serif;
  font-weight: 700;
  font-size: clamp(32px, 4vw, 48px);
  text-transform: uppercase;
  margin: 0 0 16px;
}
.steps__subtitle {
  margin: 0 auto 50px;
}
.steps__grid {
  display: grid;
  gap: 20px;
  margin-bottom: 48px;
}
.steps__cta {
  display: flex;
  justify-content: center;
}
.steps-card {
  text-align: left;
  padding: 30px 49px;
  border-radius: 12px;
  box-shadow: -7px 4px 4px 0 rgba(0, 0, 0, 0.03);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  position: relative;
}
.steps-card:nth-child(2):before,
.steps-card:nth-child(2):after {
  content: '';
  position: absolute;
  background-size: contain;
  background-repeat: no-repeat;
  background-image: url('../../img/vector.png');
  height: 90px;
  width: 55px;
}
.steps-card__icon {
  margin-bottom: 24px;
}
.steps-card__icon img {
  display: block;
  height: auto;
  width: 67px;
}
.steps-card__body {
  margin-bottom: 20px;
}
.steps-card__kicker {
  font-family: "Barlow", sans-serif;
  font-weight: 700;
  font-size: 24px;
  text-transform: uppercase;
  color: #B22029;
  margin: 0;
}
.steps-card__title {
  font-family: "Barlow", sans-serif;
  font-weight: 700;
  font-size: 24px;
  text-transform: uppercase;
  margin: 0 0 8px;
}
.steps-card__text {
  margin: 0;
}
.steps-card__number {
  display: flex;
}
.steps-card__number--inner {
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #E1C47E;
  font-family: "Barlow", sans-serif;
  line-height: 33px;
}
.steps-card__number-value {
  font-size: 48px;
  font-weight: 900;
}
.steps-card__number-label {
  font-size: 15px;
  font-weight: 700;
}
@media (max-width: 767px) {
  .steps {
    padding: 50px 0;
  }
  .steps__grid {
    grid-template-columns: 1fr;
    max-width: 420px;
    margin-left: auto;
    margin-right: auto;
  }
  .steps-card {
    min-height: auto;
  }
  .steps-card:nth-child(2):before,
  .steps-card:nth-child(2):after {
    transform: translateX(-50%) rotate(90deg);
    left: 50%;
  }
  .steps-card:nth-child(2):before {
    top: -60px;
  }
  .steps-card:nth-child(2):after {
    bottom: -50px;
  }
}
@media (min-width: 768px) {
  .steps {
    padding: 110px 0;
  }
  .steps__grid {
    grid-template-columns: repeat(3, 1fr);
  }
  .steps-card {
    min-height: 260px;
  }
  .steps-card:nth-child(2):before,
  .steps-card:nth-child(2):after {
    top: 50%;
    transform: translateY(-50%);
    height: 90px;
    width: 55px;
  }
  .steps-card:nth-child(2):before {
    left: -27px;
  }
  .steps-card:nth-child(2):after {
    transform: translateY(-50%) rotate(180deg) scaleX(-1);
    right: -48px;
    left: unset;
  }
}
